import React, { Component } from "react";
import { Modal } from "antd";

import CodeMirror from "@uiw/react-codemirror";
import "codemirror/keymap/sublime";
import "codemirror/addon/edit/closebrackets";
import "codemirror/addon/hint/show-hint";
import "codemirror/addon/hint/show-hint.css";
import "codemirror/addon/hint/css-hint";
import "antd/dist/antd.css";
import { observer, inject } from "mobx-react";

import "../utils/styleMirror.css";
import { replaceStyle } from "../utils/helper";
import {
  MARKDOWN_THEME_ID,
  TEMPLATE_CUSTOM_NUM,
  TEMPLATE_OPTIONS
} from "../utils/constant";
import TEMPLATE from "../template/index";

@inject("content")
@inject("navbar")
@observer
class StyleEditor extends Component {
  constructor(props) {
    super(props);
    this.focus = false;
  }

  getStyleInstance = instance => {
    if (instance) {
      this.styleEditor = instance.editor;
      this.styleEditor.on("keyup", (cm, e) => {
        if ((e.keyCode >= 65 && e.keyCode <= 90) || e.keyCode === 189) {
          cm.showHint(e);
        }
      });
    }
  };

  showConfirm = () => {
    Modal.confirm({
      title: "",
      content: "",
      cancelText: "",
      okText: "",
      onOk: () => {
        const { templateNum } = this.props.navbar;
        const id = TEMPLATE_OPTIONS[templateNum].id;
        const style = `/**/\n\n` + TEMPLATE.style[id];
        replaceStyle(MARKDOWN_THEME_ID, style);
        this.props.content.setCustomStyle(style);
        this.props.navbar.setTemplateNum(TEMPLATE_CUSTOM_NUM);
      },
      onCancel: () => {}
    });
  };

  changeStyle = (editor, changeObj) => {
    const { templateNum } = this.props.navbar;
    // focus
    if (this.focus && templateNum !== TEMPLATE_CUSTOM_NUM) {
      this.showConfirm();
    } else if (this.focus) {
      const style = editor.getValue();
      replaceStyle(MARKDOWN_THEME_ID, style);
      this.props.content.setCustomStyle(style);
    }
  };

  handleFocus = e => {
    this.focus = true;
  };

  handleBlur = e => {
    this.focus = false;
  };

  render() {
    return (
      <CodeMirror
        value={this.props.content.style}
        options={{
          theme: "style-mirror",
          keyMap: "sublime",
          mode: "css",
          lineWrapping: true,
          lineNumbers: false
        }}
        id="css-editor"
        onChange={this.changeStyle}
        onFocus={this.handleFocus}
        onBlur={this.handleBlur}
        ref={this.getStyleInstance}
      />
    );
  }
}

export default StyleEditor;
